import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import LocalShippingIcon from '@material-ui/icons/LocalShipping';
import { Button, List, ListItem, ListItemText } from '@material-ui/core';
import { RouteLink } from './RouteLink';

const useStyles = makeStyles((theme) => ({
  card: {
    // maxWidth: 345,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  shipping: {
    color: '#00e000',
  },
}));
const allergies = [
  { name: 'Honey', desc: 'itchiness, hives, swelling' },
  { name: 'Peanut', desc: 'eczema, sneezing, cardiac arrest' },
  {
    name: 'Lactose intolerance',
    desc: 'upset stomach, bloating, gas, diarrhea',
  },
];
export function Ordered({
  patient,
  patientPic,
  patientDiet,
  lastOrder,
  eTime,
}) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Card className={classes.card}>
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            P
          </Avatar>
        }
        action={
          <IconButton aria-label="status">
            {eTime && <LocalShippingIcon className={classes.shipping} />}
          </IconButton>
        }
        title={patient}
        subheader={
          eTime ? `Estimated time: ${eTime}` : `Last ordered: ${lastOrder}`
        }
      />

      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          Dietary order: {patientDiet}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <CardMedia
            className={classes.media}
            image={patientPic}
            title={patient}
          />
          <Typography variant="h6" paragraph>
            Last ordered:
          </Typography>
          <Typography paragraph>{lastOrder}</Typography>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            component={RouteLink}
            to="/order"
          >
            Order meal
          </Button>
          <Typography variant="h6" paragraph>
            Allergies:
          </Typography>
          <List disablePadding>
            {allergies.map(({ name, desc }) => (
              <ListItem className={classes.listItem} key={name}>
                <ListItemText primary={name} secondary={desc} />
              </ListItem>
            ))}
          </List>
        </CardContent>
      </Collapse>
    </Card>
  );
}
