import React, { useRef } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Link as RRLink } from 'react-router-dom';
import { Divider, Select, InputLabel } from '@material-ui/core';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import { Footer } from '../components/Footer';
import { RouteLink } from '../components/RouteLink';

const useStyles = makeStyles((theme) => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  cContain: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textCenter: {
    textAlign: 'center',
  },
  dividerSpacing: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export default function SignInStaff() {
  const classes = useStyles();
  const inputLabel = useRef(null);
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Staff sign in
        </Typography>
        <form className={classes.form} noValidate>
          <FormControl ref={inputLabel} variant="outlined">
            <InputLabel id="lang">Language</InputLabel>
            <Select labelId="lang" id="langselect" value={1}>
              <MenuItem value={1}>English</MenuItem>
              <MenuItem value={2}>Spanish</MenuItem>
              <MenuItem value={3}>German</MenuItem>
            </Select>
          </FormControl>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            autoComplete="username"
            type="text"
            autoFocus
            InputLabelProps={{
              shrink: true,
            }}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            InputLabelProps={{
              shrink: true,
            }}
          />
          <Typography>
            By signing in, you agree to the{' '}
            <RRLink to="tos">terms and conditions</RRLink>
          </Typography>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            component={RouteLink}
            to="/orders"
          >
            Sign in
          </Button>
          <Typography variant="h6" className={classes.textCenter}>
            or
          </Typography>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            component={RouteLink}
            to="/orders"
          >
            Scan your badge
          </Button>
          <Divider className={classes.dividerSpacing} />
          <Grid container>
            <Grid item xs>
              <Link component={RouteLink} to="/" variant="body2">
                Patient sign in
              </Link>
            </Grid>
            <Grid item>
              <Link component={RouteLink} to="/signin" variant="body2">
                Can&apos;t sign in? Contact support!
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Footer />
      </Box>
    </Container>
  );
}
